* {
  margin: 0;
  padding: 0;
  list-style: none;
  vertical-align:baseline;
}

div {
  position: relative;
  z-index: 2;
}

body {
  background-color: rgb(4, 102, 248);
  color: #fff;
  font-family: 'Raleway', sans-serif;
  font-weight: 400;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

#quote-box {
  border-radius: 3px;
  position: relative;
  width: 450px;
  padding: 20px 50px 50px 50px;
  /* display:flex; */
  background-color: #fff;
}

#text {
  text-align: center;
  width: 450px;
  height: auto;
  clear: both;
  font-weight: 500;
  font-size: 1.25em;
  color: rgb(4, 102, 248);
}

#text i {
  font-size: 1.0em;
  margin-right: 0.4em;
}

#author {
  width: 450px;
  height: auto;
  clear: both;
  padding-top: 10px;
  font-size: 1em;
  text-align: right;
  color: rgb(4, 102, 248);
}

a {
  text-decoration: none;
}

#tweet-quote, #new-quote {
  padding: 5px;
  /* padding-top: 8px; */
  text-align: center;
  font-size: 1em;
  margin-top: 5px;
  margin-right: 5px;
  height: 25px;
  width: 90px;
  color: #fff;
  background-color: rgb(4, 102, 248);
}

#tweet-quote {
  float: left;
}

#new-quote {
  float: right;
}